.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #c839ec;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.js-hiden {
  display: none;
}

